import Vue from 'vue'
import Router from 'vue-router'
import Button from '../page/Button'
import Box from '../page/Box'
import ShowCon from '../page/ShowCon'
import TabControl from '../page/TabControl'
import ColorText from '../page/ColorText'
import EditorForm from '../page/EditorForm'
import Test from '../page/Test'
import UploadFiles from '../page/UploadFiles'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Button',
      name: 'Button',
      component: Button
    },
    {
      path: '/Box',
      name: 'Box',
      component: Box
    },
    {
      path: '/ShowCon',
      name: 'ShowCon',
      component: ShowCon
    },
    {
      path: '/TabControl',
      name: 'TabControl',
      component: TabControl
    },
    {
      path: '/ColorText',
      name: 'ColorText',
      component: ColorText
    },
    {
      path: '/EditorForm',
      name: 'EditorForm',
      component: EditorForm
    },
    {
      path: '/Test',
      name: 'Test',
      component: Test
    },
    {
      path: '/UploadFiles',
      name: 'UploadFiles',
      component: UploadFiles
    },
  ]
})



